<template>
  <div id="message">
    <article class="title">
      <h2>ÁïôË®ÄÊùø</h2>
      <p>ËØ∑ÊñáÊòéÂèëË®ÄÔºåÁ¶ÅÊ≠¢ÂπøÂëä„ÄÇ‰∏çÁÑ∂ÊãâÈªë‰∫ÜÂó∑\(^o^)/~</p>
      <el-input
        type="textarea"
        :rows="2"
        placeholder="ËØ∑ËæìÂÖ•ÂÜÖÂÆπ"
        maxlength="100"
        show-word-limit
        v-model="textarea">
      </el-input>
      <div class="btn">
        <i
          class="iconfont icon-biaoqing"
          @click.stop="handleToggleEmoji"
        ></i>
        <el-button type="primary" size="mini" @click="handleMsgBtn">ÂèëË°®</el-button>
        <div
          v-show="showEmoji"
          @click.stop
          class="emoji">
          <ul>
            <li
              v-for="(item,index) in emoji"
              @click="handleEmojiClick(item)"
              :key="index"
            >
              {{item}}
            </li>
          </ul>
        </div>
        <!-- Ê∏ÖÈô§ÊµÆÂä® -->
        <div class="clear"></div>
      </div>
    </article>
    <article class="content">
      <!-- ÁïôË®ÄÈÉ®ÂàÜ -->
      <ul>
        <li v-for="(item,index) in msgData" :key="index">
          <!-- ÁïôË®Ä -->
          <div class="parent">
            <div class="left">
              <img :src="urlBase + item.user.face" alt="">
            </div>
            <div class="right">
              <div class="name-time">
                <span class="name">{{item.user.user}}</span>
                <span class="time">{{item.time | msgTime}}</span>
              </div>
              <div class="message">{{item.msg}}</div>
              <div class="like-reply">
                <i class="like iconfont icon-xinaixin" :class="{isLikes: item.likes.indexOf(userInfo._id) !== -1}" @click="hangdleLikeClick(item,item)"></i>
                <span>{{item.likes.length}}</span>
                <!-- isLike: item.likes.indexOf(userInfo._id) !== -1 -->
                <i class="reply iconfont icon-huifu" @click="hangdleReplyClick(item,item)"></i>
              </div>
            </div>
          </div>
          <!-- Â≠êÁïôË®Ä -->
          <div class="child" v-for="(childrenItem,childrenindex) in item.children" :key="childrenindex">
            <div class="left">
              <img :src="urlBase + childrenItem.user.face" alt="">
            </div>
            <div class="right">
              <div class="name-time">
                <span class="name">{{childrenItem.user.user}}</span>
                <span class="time">{{childrenItem.time | msgTime}}</span>
              </div>
              <div class="message">
                <span>@{{childrenItem.reply.user}}</span>
                {{childrenItem.msg}}
              </div>
              <div class="like-reply">
                <i class="like iconfont icon-xinaixin" :class="{isLikes:childrenItem.likes.indexOf(userInfo._id) !== -1}" @click="hangdleLikeClick(item,childrenItem,childrenindex)"></i>
                <span>{{childrenItem.likes.length}}</span>
                <i class="reply iconfont icon-huifu" @click="hangdleReplyClick(item,childrenItem)"></i>
              </div>
            </div>
          </div>
          <!-- ÂõûÂ§çÊ°Ü -->
          <div class="replybox" :class="{'show': item.isShowReply}">
            <el-input class="replyInput" v-model="item.replyText" :placeholder="'@' + item.replyUser.user" size="mini"></el-input>
            <el-button class="replyBtn" type="primary"  size="mini" @click="handleInputReply(item)">ÂõûÂ§ç</el-button>
            <!-- Ê∏ÖÈô§ÊµÆÂä® -->
            <div class="clear"></div>
          </div>
        </li>
      </ul>
    </article>
  </div>
</template>

<script>

// ÂºïÂÖ•vuex
import {mapState} from "vuex"

export default {
  naem: "Message",
  data(){
    return {
      textarea: "",
      showEmoji: false,
      emoji: [
        "üòÉ", "üòÅ", "üòÇ", "üòä", "üôÉ", "ü•∞", "üòò", "ü§≠", "ü§î", "üòè",
        "üòµ", "üòü", "üôÅ", "üò≥", "üò∞", "üò≠", "üòñ", "üò°", "üò†", "üòà",
        "ü§°", "‚ò†Ô∏è", "üëª", "üôà", "üôâ", "‚ù§Ô∏è", "üíñ", "üí¢", "üí£", "üí§",
        "üëã", "üëç", "üëå", "ü§û", "ü§ü", "ü§ô", "üëä", "üñï", "ü§ù", "üôè",
        "üôÖ", "üôÖ‚Äç‚ôÇÔ∏è", "üë®‚Äç‚úàÔ∏è", "üë©‚Äç‚úàÔ∏è", "üë∑", "üë∑‚Äç‚ôÄÔ∏è", "ü§¶", "ü§¶‚Äç‚ôÇÔ∏è", "üë¥", "üßì",
        "üê¥", "üêÆ", "üêñ", "üêë", "üêì", "ü¶ö", "üê≤", "üê≥", "üêü", "üêå",
      ],
      msgData: [],
        //  Âà§Êñ≠ÊòØÂê¶ÊòØÂºÄÂèëÁéØÂ¢É  
        urlBase: process.env.NODE_ENV === "development"?"http://localhost:3000/":""

    }
  },
  computed: {
    ...mapState(["userInfo"])
  },
  // ËøáÊª§Âô®
  filters: {
    // ÂØπÁïôË®ÄÊó∂Èó¥ËøõË°åÂ§ÑÁêÜ
    msgTime(value){
      let date = new Date(value);
      let YY = date.getFullYear();
      let MM = date.getMonth() + 1;
      let DD = date.getDate();
      let hh = date.getHours();
      let mm = date.getMinutes();
      let ss = date.getSeconds();


      // ÂèåÊï∞Â≠óÂ§ÑÁêÜ
      MM = (MM < 10 ? "0" : "") + MM;
      DD = (DD < 10 ? "0" : "") + DD;
      hh = (hh < 10 ? "0" : "") + hh;
      mm = (mm < 10 ? "0" : "") + mm;
      ss = (ss < 10 ? "0" : "") + ss;

      return `${YY}-${MM}-${DD}  ${hh}:${mm}:${ss}`
    }
  },
  methods:{
    //ÂàáÊç¢emojiÁöÑÊòæÁ§∫‰∏éÈöêËóè
    handleToggleEmoji(){
      this.showEmoji = !this.showEmoji
    },

    //ÁÇπÂáªÊØè‰∏Ä‰∏™emojiÁöÑÊó∂ÂÄôÔºåÂä†ÂÖ•input
    handleEmojiClick(item){
      this.textarea += item
      this.showEmoji = false
    },

    // ËØ∑Ê±ÇÁïôË®ÄÊï∞ÊçÆ
    async getMessageData(){
      let {data} = await this.$axios({
        method: "GET",
        url: "/msg/data",
      })
      data.data.filter(item =>{
        // ÊØèÊù°ÁïôË®ÄÂõûÂ§çÁöÑÂÜÖÂÆπ
        item.replyText = ""
        // ËÆ∞ÂΩïÂõûÂ§çÈÇ£‰∏™Áî®Êà∑
        item.replyUser = {...item.user}
        // ËÆ∞ÂΩïÂõûÂ§çÊ°ÜÊòæÁ§∫ÊàñÈöêËóè
        item.isShowReply = false

      })
      
      this.msgData = data.data

    },


    // ÁïôË®Ä‰ø°ÊÅØÊèê‰∫§
    async handleMsgBtn(){
      // Ê£ÄÊµãÁî®Êà∑ÊòØÂê¶ÁôªÂΩï
      let userId = this.userInfo._id
      if(!userId){
        return this.$message.error("ËØ∑ÂÖàÁôªÂΩïÔºåÂÜçÁïôË®ÄÔºÅÔºÅÔºÅ")
      }

      // Ê£ÄÊµãÁïôË®ÄÂÜÖÂÆπÊòØÂê¶ÊúâÊï∞ÊçÆ
      let msg = this.textarea.trim();
      if(!msg){
        return this.$message.error("ËØ∑ÂÖàËæìÂÖ•ÁïôË®ÄÂÜÖÂÆπ~")

      }
      // ÂêëÂêéÁ´ØÂèëÈÄÅËØ∑Ê±Ç
      let {data} = await this.$axios({
        method: "POST",
        url: "/msg",
        data: {
          userId,
          msg
        }
      })

      // È™åËØÅÁïôË®Ä‰ø°ÊÅØ
      if(data.code !== 0){
        return this.$message.error(data.msg)
      }
      //  È™åËØÅÊàêÂäü
      this.$message({
        type: "success",
        message: data.msg,
        duration: 1000,
        onClose(){
          // Âà∑Êñ∞È°µÈù¢
          window.location.reload()
        }
      })
    },


    // ÁÇπÂáªliakes
    async hangdleLikeClick(item, thisItem,childrenindex){
      // Âà§Êñ≠ÊòØÂê¶ÁôªÂΩï
        let userId = this.userInfo._id;
        if(!userId){
          return this.$message.error("ËØ∑ÂÖàÁôªÂΩï")
        }

        // Âà§Êñ≠ÊòØÁÇπËµûËøòÊòØÂèñÊ∂àËµû
        let likes = thisItem.likes
        let index = likes.indexOf(userId)
        if(index !== -1){
          // ÂèñÊ∂àÁÇπËµû
          likes.splice(index, 1)
        }else{
          // ÁÇπËµû
          likes.push(userId)
        }

        // Â∞ÜÊï∞ÊçÆÂèëÈÄÅÂà∞Êï∞ÊçÆÂ∫ì
        let {data} = await this.$axios({
          method: "POSt",
          url: "/msg/like",
          data: {
            parentId: item._id,
            childId: thisItem._id,
            likes,
            childrenindex
          }
        })
    },
    // ÁÇπÂáªÂõûÂ§çÊåâÈíÆÂºπÂá∫ÂõûÂ§çÂØπËØùÊ°Ü
    async hangdleReplyClick(item,thisItem){
        if(thisItem.user._id === item.replyUser._id && item.isShowReply){
          item.isShowReply = false
          return
        }
        item.replyUser = thisItem.user
        item.isShowReply = true
        
    },
    //  ÂõûÂ§çÂØπËØùÊ°ÜÊèê‰∫§
    async handleInputReply(item){
      // ÂΩìÂâçÁôªÂΩïÁî®Êà∑
        let user = this.userInfo._id;
        // ÂõûÂ§çÁöÑË∞Å
        let reply = item.replyUser._id;
        
        // ÂõûÂ§çÁöÑÂÜÖÂÆπ
        let msg = item.replyText.trim();
        if(!user){
          return this.$message.error("ËØ∑ÂÖàÁôªÂΩï")
        }
        if(!msg){
          return this.$message.error("ËØ∑ÂÖàËæìÂÖ•ÁïôË®ÄÂÜÖÂÆπ")
        }
        // Ê•º‰∏ªId
        let msgId = item._id;
        // ÁïôË®Ä‰ø°ÊÅØ

        let child = {msg, user, reply}
        let {data} = await this.$axios({
          method: "POST",
          url: "/msg/reply",
          data: {msgId, child}
        })
        console.log(reply)
        if(data.code !== 0){
          return this.$message.error(data.msg)
        }
        this.$message({
          type: "success",
          message: data.msg,
          duration: 1000,
          onClose(){
            window.location.reload()
          }
        })
    }
  },
  mounted() {
    // ÁÇπÂáª‰ªªÊÑèÂú∞ÊñπÔºåÂÖ≥Èó≠emoji
    document.addEventListener("click",()=>{
      this.showEmoji = false
    })

    // ÂêëÂêéÁ´ØËØ∑Ê±ÇÁïôË®ÄÊï∞ÊçÆ
    this.getMessageData()
  }
}
</script>
<style lang="less" scoped>
#message {
  user-select: none;
  article.title {
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 15px;
    box-shadow: 0 0 4px #ddd;
    padding: 25px 20px;
    background-color: #fff;
    font-family: "Quicksand", "Microsoft YaHei", sans-serif;
      h2 {
        margin-bottom: 15px;
        font-size: 20px;
        letter-spacing: 3px;
        line-height: 24px;
        border-left: 5px solid #73b899;
        text-indent: 10px;
      }
      p {
        text-indent: 2em;
        color: #444;
        font-size: 14px;
        letter-spacing: 4px;
        line-height: 30px;

        span {
          color: #bd4147;
          background-color: rgba(27, 31, 35, .05);
          font-weight: 700;
        }

        a {
          color: #409eff;

          &:hover {
            text-decoration: underline;
          }
        }
      }
        div.btn{
          position: relative;
          i{
            float: left;
            margin-top: 5px;
            font-size: 20px;
            cursor: pointer;
          }
          .el-button{
            float: right;
            margin-top: 5px;
          }
          .emoji{
            position: absolute;
            top: 30px;
            background-color: #fff;
            box-shadow: 0 0 3px #aaa;

            ul{
              width: 250px;
              height: 150px;
              margin: 8px;
              border-left: 1px solid #ddd;
              border-bottom: 1px solid #ddd;
              cursor: pointer;
              li{
                box-sizing: border-box;
                float: left;
                font-size: 18px;
                width: 25px;
                height: 25px;
                line-height: 22px;
                border-right: 1px solid #ddd;
                border-top: 1px solid #ddd;
              }
            }
          }
        }
  }


  article.content{
    background-color: #fff;
    ul{ 

        width: 970px;
        margin-left: 20px;
        padding-top: 15px;
        margin-right: 20px;
        li{
          padding-bottom: 8px;
          margin-bottom: 14px;
          font-size: 12px;
          border-bottom: 1px dashed #ddd;
          font-family: "Quicksand","Microspft YaHei","sans-serif";
          div.parent,div.child{
          display: flex;
          }
          div.child{
            margin-left: 55px;
            margin-top: 3px;
          }
          div.left{
            overflow: hidden;
            width: 40px;
            height: 40px;
            margin-right: 15px;
            border-radius: 50%;
            img{
              width: 100%;
              height: 100%;
            }
          }
          div.right{
            flex: 1;
            div.name-time{
              font-size: 12px;
              margin-bottom: 3px;
              span:nth-child(1){
                color: #73b899;
              }
              span:nth-child(2){
                color: #aaa;
                font-size: 12px;
                margin-left: 10px;
              }
              span.name{
                cursor: pointer;
              }
            }
            div.message{
              color: #666;
              letter-spacing: 3px;
              margin-bottom: 3px;
              word-break: break-word;
              span{
                color: #409eff;
                margin-right: 5px;
                cursor: pointer;
              }
            }
            div.like-reply{
              i{
                cursor: pointer;
                font-size: 14px;
                color: #aaa;

              }
              i.isLikes{
                color: rgb(255, 32, 32);
              }
              span{
                display: inline-block;
                font-size: 12px;
                width: 42px;
                margin-left: 2px;
                color: #aaa;
              }
            }
          }
          .replybox{
            overflow: hidden;
            margin-left: 55px;
            height: 0;
            box-sizing: border-box;
            transition: height .5s;
            &.show{
              height: 54px;
            }
            .replyInput{
              margin-bottom: 4px;
              width: 100%;
            }
            .replyBtn{
              padding: 4px 8px !important;
              float: right;
            }  
          }
        }

    }    
  }
  
} 


</style>